import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { ExceptionManagementComponent } from '../exception-management/exception-management.component';
import { AdjustmentManagementComponent } from '../adjustment-management/adjustment-management.component';
import { ViewDataflowComponent } from '../view-dataflow/view-dataflow.component';

const appRoutes: Routes = [

  { path: 'exception-management', component: ExceptionManagementComponent },
  { path: 'adjustment-management', component: AdjustmentManagementComponent },
  { path: 'view-dataflow', component: ViewDataflowComponent },
 {path: '**', redirectTo:'exception-management',pathMatch: 'full'}

]

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forRoot(appRoutes)
  ],
  exports: [
    RouterModule
  ],
  declarations: []
})
export class AppRoutingModule { }



