import { Component, OnInit } from '@angular/core';
import { GridOptions } from 'ag-grid/dist/lib/entities/gridOptions';
import { ExceptionComponent } from './exception.component';
import {ExceptionService} from './exception.service'
import {Http, Response, RequestOptions, Headers} from '@angular/http';

@Component({
  selector: 'app-exception-management',
  templateUrl: './exception-management.component.html',
  styleUrls: ['./exception-management.component.scss']
})
export class ExceptionManagementComponent implements OnInit {
private gridOptions: GridOptions;
  rowData: any;
  rowDatacopy:any;
   rowDatacopy1:any;
   productdetails:any;
  constructor(private exceptionService:ExceptionService, private http: Http) { 
    this.gridOptions = <GridOptions>{
      context: {
        componentParent: this
      },

      pagination: true, 
      enableFilter: true,
      enableSorting: true,
      floatingFilter: true,
      enableColResize:true,
   
    }

   this.gridOptions.rowHeight = 25;
this.gridOptions.columnDefs = [
{ headerName: 'ExceptionId', field: 'exceptionId', width: 160 },
{ headerName: 'Date of Exception', field: 'dateofexception', width: 180 },
{ headerName: 'Description', field: 'description', width: 220 },
{ headerName: 'Status', field: 'status', width: 120 },
{ headerName: 'Actions',cellRendererFramework: ExceptionComponent, width: 120 },
{ headerName: 'EntityType', field: 'entityType', width: 150 },
{ headerName: 'Entityid', field: 'entityId', width: 130 },
];
} 

  public methodFromParent(rowDatacopy:any) {
        this.rowDatacopy = this.rowData;
    }
 public statusmethodFromParent(partydata){          
   
    this. exceptionService.datasmethodFromParent(partydata.entityId)
             .subscribe(
               Data => {
                   this.productdetails = Data[0];
               })
    }


 getallData(){
 this.http.get(`http://localhost:8088/exceptiondetails`).subscribe(data => { 
    this.rowData=data.json().reverse();
 });
}


 ngOnInit() {
   this.getallData();
   
  }
}


