import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';


import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { ExceptionManagementComponent } from './exception-management/exception-management.component';
import { AdjustmentManagementComponent } from './adjustment-management/adjustment-management.component';
import { ViewDataflowComponent } from './view-dataflow/view-dataflow.component';
import {AgGridModule} from "ag-grid-angular/main";
import { NavComponent } from './nav/nav.component';
import { AppRoutingModule } from './app-routing/app-routing.module';
import { ExceptionComponent } from './exception-management/exception.component';
import { HttpClientModule } from '@angular/common/http';
import {ExceptionService} from './exception-management/exception.service'
import {HttpModule} from '@angular/http';


@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    ExceptionManagementComponent,
    AdjustmentManagementComponent,
    ViewDataflowComponent,
    
    NavComponent,
    ExceptionComponent
    
  ],
  imports: [
    BrowserModule,
    AgGridModule.withComponents([ ExceptionComponent]),
    AppRoutingModule,
    HttpClientModule,
    HttpModule
  ],
  providers: [ExceptionService ],
  bootstrap: [AppComponent]
})
export class AppModule { }



