import { Injectable } from '@angular/core';
import { HttpClient,HttpErrorResponse, HttpHeaders,HttpRequest, HttpEvent, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class ExceptionService {
   
    
  constructor(private http: HttpClient) { }

datasmethodFromParent(pty_ID): Observable<any> {
    return this.http.get(`http://localhost:8088/partydetails?tempId=` +pty_ID)          
     }
}



