
import {Component, TemplateRef} from "@angular/core"; 

import {ICellRendererAngularComp} from "ag-grid-angular";

@Component({
    selector: 'exception-management-exception',
    templateUrl: './exception.html'
})
export class ExceptionComponent implements ICellRendererAngularComp {
    public params: any;
    showPlayButton:boolean;
    showHideButtons: boolean ;
    constructor( ) {
    }

    agInit(params: any): void {
        this.params = params;
        if(typeof params.data == undefined || params.data == null){
            this.showPlayButton = false;
        }else{
            this.showPlayButton = true;
        }
    }
  
     public invokeParentMethod() {
         this.params.context.componentParent.
         methodFromParent(this.params.data);
     }
      public statusinvokeParentMethod() {
            this.params.context.componentParent.
        statusmethodFromParent(this.params.data);
        
     }

     refresh(): boolean {
         return false;
     }

   
     ngOnInit() {
    }
} 



